import React from 'react'
import { Menu, Icon, Pagination } from 'semantic-ui-react'

const RoundPagination = ({total, selected, style, ...props}) => {
  const pages = Array.from(Array(total+1).keys()).slice(1)
  const menuStyle = {
    margin: 0,
    backgroundColor: 'black',
    overflow: 'hidden',
    display: 'flex',
    ...style
  }
  return (
    <Menu borderless inverted style={menuStyle} {...props}>
      <Menu.Item header>Round</Menu.Item>
      <div className="ui pagination menu hide-scrollbar inverted" style={{background: 'black'}}>
        {
          pages.map(page => (
            <a key={`page-${page}`} className={`item ${page === selected ? 'active' : ''}`} onClick={_ => props.onChange(page)}>{page}</a>
          ))
        }
      </div>
    </Menu>
  )
}

export default RoundPagination
