var ProtoEncoder = require('./schema/protobuf/Encoder.proto');

var AvroEncoder = require('./schema/avro/Encoder.avro');

module.exports = function (type) {
  return type === 'avro' ? AvroEncoder : ProtoEncoder;
};

module.exports.decodeGame = function (game, type) {
  return type === 'avro' ? AvroEncoder.decodeGame(game) : ProtoEncoder.decodeGame(game);
};