import React, { Component } from 'react'
import { Header, Modal, Icon, Progress, Divider } from 'semantic-ui-react'
import Parser from 'gotactics-parser'
import { DemoPicker } from './FilePicker'
import DropZone from 'react-dropzone'

export default class DemoPickerModal extends Component {
  static defaultProps = {
    onComplete: () => {}
  }

  initialState = {
    progress: 0,
    game: undefined
  }

  constructor(props) {
    super(props)
    this.state = this.initialState
    this.parser = new Parser()
    this.parser.on('start', this.onStart.bind(this))
    this.parser.on('progress', this.onProgress)
    this.parser.on('end', this.onEnd)
  }

  onDemoSelect = file => {
    this.setState({
      ...this.initialState,
      fileName: file.name,
    }, () => {
      this.readFile(file).then(buffer => {
        if (/.dem/.test(file.name)) {
          this.parser.parse(buffer)
        }

        if (/.replay/.test(file.name)) {
          this.onEnd(Buffer.from(buffer))
        }
      })
    })

  }

  onDrop = files => {
    this.onDemoSelect(files.pop())
  }

  readFile(file) {
    return new Promise((resolve, reject) => {
      var reader = new FileReader()
      reader.onload = e => resolve(e.target.result)
      reader.readAsArrayBuffer(file)
    })
  }

  onEnd = game => {
    this.setState({ progress: 100, game }, _ => {
      setTimeout(() => {
        this.props.onComplete(game)
      }, 1000)
    })
  }

  onStart = header => {
    const validMap = [
      'de_overpass',
      'de_nuke',
      'de_cache',
      'de_inferno',
      'de_mirage',
      'de_cbble',
      'de_train',
      'de_dust2'
    ].includes(header.mapName)

    if (!validMap) {
      this.parser.cancel()
      window.alert(`We don't support the map ${header.mapName}`)
      this.setState({ progress: 0, fileName: undefined})
    } else {
      this.setState({ progress: 0})
    }

  }

  onProgress = (progress) => {
    if (progress - this.state.progress > 1 || progress === 100) {
      this.setState({progress})
    }
  }

  render() {
    const { fileName } = this.state
    const { onComplete, ...props } = this.props
    return (
      <Modal
        dimmer='blurring'
        size='small'
        {...props}
      >
        <Modal.Content>
          {! fileName &&
            <div>
              <DemoPicker
                color='teal'
                fluid
                onSelect={this.onDemoSelect}
                content='Select demo file'
              />
              <Divider horizontal>Or</Divider>
              <DropZone
                disableClick
                multiple={false}
                accept=".dem,.replay"
                onDrop={this.onDrop}
                style={{width: 'auto', height: 200, display: 'flex', justifyContent: 'center', alignItems: 'center'}}
                activeStyle={{backgroundColor: '#e5e5e5'}}
              >
                <Header icon textAlign='center'>
                  <Icon name='file outline' circular />
                  <Header.Content>Drag demo file here</Header.Content>
                </Header>
              </DropZone>
            </div>
          }
          { fileName &&
            <div>
              <Progress
                size='large'
                color='teal'
                precision={0}
                percent={this.state.progress}
                autoSuccess progress
                label={this.state.fileName}
              />
            </div>
          }
        </Modal.Content>
      </Modal>
    )
  }
}

