import React from 'react'
import PlayerCircle from './PlayerCircularInfo'
import Weapon, { getOrder } from './Weapon'

/**
 * Player Row
 * @example ./examples/PlayerRow.md
 */
const PlayerRow = ({weapons = [], weapon = null, ...props}) => {

  const [primary, ...others] = weapons
    .filter(w => ![8,9].includes(getOrder(w)))
    .sort((a,b) => getOrder(a) - getOrder(b))

  const style = {
    display: 'flex',
    alignItems: 'center',
    color: 'rgba(255, 255, 255, 0.7)',
    position: 'relative'
  }

  return (
    <div style={style}>

      { props.flashDuration > 0 &&
        <div style={{position: 'absolute', backgroundColor: 'rgba(255,255,255,0.5)', top: 0, left: 0, right: 0, bottom: 0}}/>
      }

      <PlayerCircle
        health={props.health}
        hasDefuser={props.hasDefuser}
        hasArmor={props.armor > 0}
        hasHelmet={props.hasHelmet}
        side={props.side}
        textColor='#ccd0d5'
        size={45}
        style={{flexShrink: 0}}
      />

      <div style={{flexGrow: 1, textOverflow: 'ellipsis', overflow: 'hidden', margin: '0 10px'}}>{props.name}</div>

      <div style={{display: 'flex', justifyContent: 'flex-end', margin: '0 5px'}}>
        {primary &&
          <div style={{width: 80}}>
            <Weapon
              id={primary}
              height={15}
              style={{margin: '0 3'}}
              fill='white'
              fillOpacity={primary === weapon ? 1 : 0.5}/>
          </div>
        }

        <div style={{minWidth: 100}}>
          { others.map(w => (
            <Weapon
              key={w}
              id={w.toString()}
              height={15}
              style={{margin: '0 3'}}
              fill='white'
              fillOpacity={w === weapon ? 1 : 0.5}/>
            ))
          }
        </div>
      </div>

      { parseInt(props.health, 10) === 0 &&
        <div style={{position: 'absolute', backgroundColor: 'rgba(0,0,0,0.5)', top: 0, left: 0, right: 0, bottom: 0}}/>
      }

    </div>
  )
}

PlayerRow.propTypes = {
  ...PlayerCircle.propTypes
}

export default PlayerRow

