import React, { Component } from 'react'
import { Progress } from 'semantic-ui-react'

const getElementPosition = element => {
  let x = 0, y = 0;

  do {
    x += element.offsetLeft;
    y += element.offsetTop;
    element = element.offsetParent
  }
  while (element);

  return { x, y }
}

export default class EditableProgress extends Component {

  onClick = e => {
    const onChange = this.props.onChange
    const elementPosition = getElementPosition(e.currentTarget)
    const clickPosition = { x: e.clientX, y: e.clientY }
    const clickWidth = clickPosition.x - elementPosition.x
    const clickPercent = (clickWidth / e.currentTarget.offsetWidth) * 100

    if (onChange) {
      onChange(clickPercent, this.state.dragging)
    }
  }

  onMouseUp = e => {
    this.setState({dragging: false})
  }

  onMouseDown = e => {
    this.setState({dragging: true})
  }

  onMouseLeave = e => {
    // this.setState({dragging: false})
  }

  onMouseMove = e => {
    if (this.state && this.state.dragging) {
      this.onClick(e)
    }
  }

  render() {
    const barStyle = {
      padding: 5
    }

    const percent = (this.props.value / this.props.total) * 100

    const thumbStyle = {
      width: `${percent}%`,
      height: '100%',
      backgroundColor: '#303030',
      margin: 0
    }

    return (
      <div
        style={{...this.props.style, ...barStyle}}
        onClick={this.onClick}
        onMouseDown={this.onMouseDown}
        onMouseUp={this.onMouseUp}
        onMouseLeave={this.onMouseLeave}
        onMouseMove={this.onMouseMove}
      >
        <div style={thumbStyle}/>
      </div>
    )
  }
}

