var avro = require('avsc');

var Position = {
  type: 'record',
  fields: [{
    name: 'x',
    type: 'float'
  }, {
    name: 'y',
    type: 'float'
  }, {
    name: 'z',
    type: 'float'
  }]
};
var Event = {
  name: 'Event',
  type: 'record',
  fields: [{
    name: 'type',
    type: 'string'
  }, {
    name: 'body',
    type: 'string'
  }, {
    name: 'tick',
    type: 'int'
  }, {
    name: 'time',
    type: 'float'
  }]
};
var Player = {
  name: 'Event',
  type: 'record',
  fields: [{
    name: 'hasC4',
    type: 'boolean',
    default: false
  }, {
    name: 'hasDefuser',
    type: 'boolean',
    default: false
  }, {
    name: 'hasHelmet',
    type: 'boolean',
    default: false
  }, {
    name: 'shotsFired',
    type: 'int',
    default: 0
  }, {
    name: 'weapon',
    type: ['null', 'int'],
    default: null
  }, {
    name: 'armor',
    type: 'int'
  }, {
    name: 'health',
    type: 'int'
  }, {
    name: 'direction',
    type: 'int'
  }, {
    name: 'steam64Id',
    type: 'string'
  }, {
    name: 'teamNumber',
    type: 'int'
  }, {
    name: 'flashDuration',
    type: 'float'
  }, {
    name: 'name',
    type: 'string'
  }, {
    name: 'position',
    type: Position
  }, {
    name: 'weapons',
    type: {
      type: 'array',
      items: 'int'
    }
  }]
};
var Grenade = {
  name: 'Grenade',
  type: 'record',
  fields: [{
    name: 'type',
    type: ['null', 'string'],
    default: null
  }, {
    name: 'position',
    type: Position
  }, {
    name: 'isDetonated',
    type: 'boolean',
    default: false
  }]
};
var Frame = avro.Type.forSchema({
  name: 'Frame',
  type: 'record',
  fields: [{
    name: 'players',
    type: {
      type: 'array',
      items: Player
    }
  }, {
    name: 'grenades',
    type: {
      type: 'array',
      items: Grenade
    }
  }, {
    name: 'time',
    type: 'float'
  }, {
    name: 'c4position',
    type: ['null', Position],
    default: null
  }, {
    name: 'c4status',
    type: 'string'
  }]
});
var Round = {
  name: 'Round',
  type: 'record',
  fields: [{
    name: 'startTick',
    type: 'int'
  }, {
    name: 'startTime',
    type: 'float'
  }, {
    name: 'endTick',
    type: 'int'
  }, {
    name: 'endTime',
    type: 'float'
  }, {
    name: 'number',
    type: 'int'
  }, {
    name: 'score',
    type: {
      type: 'map',
      values: 'int'
    }
  }, {
    name: 'events',
    type: {
      type: 'array',
      items: Event
    }
  }]
};
var Game = avro.Type.forSchema({
  name: 'Game',
  type: 'record',
  fields: [{
    name: 'rounds',
    type: {
      type: 'array',
      items: Round
    }
  }, {
    name: 'frames',
    type: {
      type: 'map',
      values: 'bytes'
    }
  }, {
    name: 'mapName',
    type: 'string'
  }, {
    name: 'tickRate',
    type: 'float'
  }, {
    name: 'playbackTime',
    type: 'float'
  }, {
    name: 'playbackTicks',
    type: ['null', 'int'],
    default: null
  }, {
    name: 'playbackFrames',
    type: ['null', 'int'],
    default: null
  }]
});
module.exports = {
  Game: Game,
  Frame: Frame
};