import React from 'react'
import PropTypes from 'prop-types'

/**
 * Circular Progress component
 * @example ./examples/CircularProgress.md
 */
const CircularProgress = props => {
  const {
    text="",
    percentage = 0,
    radius = 50,
    strokeWidth = 12,
    backgroundColor = '#a6a6a6',
    progressColor = '#e6e6e6',
    textColor = '#a6a6a6',
    fontSize = '20px',
    size
  } = props

  let r = radius - strokeWidth
  var circumference = 2 * Math.PI * r;
  var progress = percentage / 100;
  var dashoffset = circumference * (1 - progress);

  let textStyle = {
    fill: textColor,
    fontSize: fontSize,
    dominantBaseline: 'middle',
    textAnchor: 'middle'
  }

  return (
    <svg viewBox='0 0 100 100' width={size} height={size}>
      <g transform='rotate(-90,50,50)'>
        <circle cx="50" cy="50" r={r} fill="none" stroke={backgroundColor} strokeWidth={strokeWidth} />
        <circle cx="50" cy="50" r={r} fill="none" stroke={progressColor} strokeWidth={strokeWidth}
          strokeDasharray={circumference}
          strokeDashoffset={dashoffset}
        />
      </g>
      <text x="50" y="55" style={textStyle}>
        {text}
      </text>
    </svg>
  )
}

CircularProgress.propTypes = {
  /** Text to display inside the progress circle. */
  text: PropTypes.string,

  /** Amount of progress to display. */
  percentage: PropTypes.number,

  /** Circular radius. */
  radius: PropTypes.number,

  /** Width of the progress border. */
  strokeWidth: PropTypes.number,

  /** Color of the brackground circle. */
  backgroundColor: PropTypes.string,

  /** Color of the progress circle. */
  progressColor: PropTypes.string,

  /** Inner text color. */
  textColor: PropTypes.string,

  /** Inner text size. */
  fontSize: PropTypes.number,
}

export default CircularProgress

