var Schema = require('./schema.avro');

var encodeGame = function encodeGame(game) {
  return Schema.Game.toBuffer(game);
};

var decodeGame = function decodeGame(game) {
  return Schema.Game.fromBuffer(game);
};

var encodeFrame = function encodeFrame(frame) {
  return Schema.Frame.toBuffer(frame);
};

var decodeFrame = function decodeFrame(frame) {
  return Schema.Frame.fromBuffer(frame);
};

module.exports = {
  encodeGame: encodeGame,
  decodeGame: decodeGame,
  encodeFrame: encodeFrame,
  decodeFrame: decodeFrame
};