
import { findDOMNode } from 'react-dom'
import React from 'react'

class Flashbang extends React.Component {
  componentDidUpdate({ detonating }) {
    if (this.animation && this.props.detonating !== detonating) {
      this.animation.beginElement()
    }
  }

  render() {
    const { x, y, detonating } = this.props
    return (
      <circle
        fill='#3899eb'
        fillOpacity={1}
        r={2}
        cx={x}
        cy={y}
      >
      <animate
        ref={el => this.animation = el}
        attributeName="r"
        attributeType="XML"
        from={detonating ? 2 : 12}
        to={detonating ? 12 : 2}
        dur="0.2s"
        fill="freeze"
      />
      </circle>
    )
  }
}

const Hegrenade = ({x, y, detonating}) => (
  <circle
    fill='#be392a'
    fillOpacity={detonating ? 0.5 : 1}
    r={detonating ? 12 : 2}
    cx={x}
    cy={y}
  />
)
const Decoy = ({x, y, detonating}) => (
  <circle
    fill='#d9d809'
    fillOpacity={detonating ? 0.5 : 1}
    r={2}
    cx={x}
    cy={y}
  />
)

const Molotov = ({x, y, detonating}) => (
  <circle
    fill='#ff6014'
    fillOpacity={detonating ? 0.5 : 1}
    r={detonating ? 12 : 2}
    cx={x}
    cy={y}
  />
)

const Smoke = ({x, y, detonating}) => (
  <circle
    fill='#ffffff'
    fillOpacity={detonating ? 0.5 : 1}
    r={detonating ? 16 : 2}
    cx={x}
    cy={y}
  />
)

/**
 * GrenadePoint
 * @example ./examples/GrenadePoint.md
 */
const GrenadePoint = props => {
  switch(props.type) {
    case 'flashbang': return (<Flashbang {...props}/>)
    case 'hegrenade': return (<Hegrenade {...props}/>)
    case 'decoy': return (<Decoy {...props}/>)
    case 'molotov': return (<Molotov {...props}/>)
    case 'incgrenade': return (<Molotov {...props}/>)
    case 'smokegrenade': return (<Smoke {...props}/>)
    default: return null
  }
}

GrenadePoint.propTypes = {
}

GrenadePoint.types = [
  'flashbang', 'hegrenade', 'decoy', 'molotov', 'incgrenade', 'smokegrenade'
]
export default GrenadePoint

