import React from 'react';
import Weapon from './Weapon'

/**
 * C4Point
 * @example ./examples/C4Point.md
 */
const C4Point = ({
  size = 15,
  status = 'dropped',
  ...props
}) => {
  return (
    <svg viewBox="0 0 30 30" width={size} height={size} x={props.x} y={props.y} style={{overflow: 'visible'}}>
      <defs>
        <filter id="f1" x="-50%" y="-50%" width="200%" height="200%">
          <feGaussianBlur in="SourceGraphic" stdDeviation="1" />
        </filter>
      </defs>

      <g>
        { status === 'planted' && <circle r={25} cy={15} cx={15} fill='red' fillOpacity={0.3} /> }
        { status === 'exploded' && <circle r={45} cy={15} cx={15} fill='red' fillOpacity={0.8} /> }

        { status === 'dropped'
          ? <Weapon id="c4" width={20} height={20} y={5} x={5} fill='red' fillOpacity={0.8}/>
          : <Weapon id="c4-ticking" width={30} height={30} fill='red'/>
        }
      </g>

    </svg>
  )
}

C4Point.propTypes = {
}

export default C4Point

