import React, { Component } from 'react'
import Weapon from './Weapon'
import { Popup } from 'semantic-ui-react'
import colors from './colors'

const moment = require('moment')

const DeathEvent = ({event}) => {
  return (
    <div>
      <span>{event.attacker}</span>
      <Weapon id={event.weapon} height={8} fill='white' style={{margin: '0 5px'}}/>
      <span>{event.victim}</span>
    </div>
  )
}

const TimelineEvent = ({event, x, time, type}) => {
  const body = event.body
  const duration = moment.duration(parseInt(time, 10), 'seconds');
  const durationString = moment.utc(duration.asMilliseconds()).format("mm:ss");

  const eventColor = type === 'death'
    ? colors[body.attackerTeamNumber]
    : colors[event.side]

  const Body = type === 'death'
    ? (<DeathEvent event={body} />)
    : (<span>{event.body}</span>)

  const popupStyle = {
    backgroundColor: eventColor,
    display: 'flex',
    fontWeight: 'bold',
    fontSize: 10,
    whiteSpace: 'nowrap',
    boxShadow: 'none',
    border: 'none'
  }

  const style = {
    position: 'absolute',
    background: eventColor,
    width: 2,
    left: x,
    top: 0,
    bottom: 0,
    cursor: 'pointer',
    zIndex: 9,
  }

  return (
    <Popup
      basic
      style={popupStyle}
      trigger={<div style={style} />}
      position="top center"
    >
      <Popup.Content style={{display: 'flex', color: 'white'}}>
        <span style={{marginRight: 15}}>{durationString}</span>
        {Body}
      </Popup.Content>
    </Popup>
  )
}

export default class TimelineBar extends Component {
  state = {}

  handleRef = container => {
    this.setState({container})
  }

  render() {
    const style = {
      position: 'absolute',
      left: 0, top: 0,
      right: 0, bottom: 0
    }

    const width = this.state.container
      ? this.state.container.getBoundingClientRect().width
      : 0

    const events = this.props.events
      .map(e => ({
        ...e,
        x: e.time * width / this.props.max
      }))
      .filter(e => e.x > 0)

    return (
      <div ref={this.handleRef} style={style}>
        {events.map((event, i) => (
          <TimelineEvent key={`key-${i}`}
            event={event}
            type={event.type}
            time={event.time}
            x={event.x}
          />
        ))}
      </div>
    )
  }
}

