import React from 'react';
import Weapon from './Weapon'
import colors from './colors'

/**
 * DeathNotice
 * @example ./examples/DeathNotice.md
 */
const DeathNotice = props => {
  const {
    victim, attacker,
    weapon,
    attackerTeamNumber
  } = props

  const popupStyle = {
    backgroundColor: colors[attackerTeamNumber],
    display: 'flex',
    fontWeight: 'bold',
    fontSize: 10,
    whiteSpace: 'nowrap',
    boxShadow: 'none',
    border: 'none',
    alignItems: 'center',
    padding: '5px 10px',
    borderRadius: 5,
    color: 'white'
  }

  return (
    <div>
      <div style={popupStyle}>
        <span>{attacker}</span>
        <Weapon id={weapon} height={8} fill='white' style={{margin: '0 5px'}}/>
        <span>{victim}</span>
      </div>
    </div>
  )
}

export default DeathNotice

