import React from 'react'
import PropTypes from 'prop-types'
import colors from './colors'

/**
 * Player Point
 * @example ./examples/PlayerPoint.md
 */
const PlayerPoint = props => {
  const {
    side = 'spec',
    direction = 0,
    size = 10,
    position = { x: 0, y: 0 },
    hasC4 = false,
    flashDuration = 0,
    isScoped = false,
    shotsFired = false,
  } = props

  const textPlace = {
    x: position.x < 150 ? 0 : -100,
    y: position.y < 150 ? 150 : -100
  }

  const rotation = `rotate(${45 + direction}, 0, 0)`
  const translate = 'translate(-45, -45)'
  const shotsDelta = isScoped ? 60 : 0

  return (
    <svg
      viewBox="0 0 90 90"
      width={size}
      height={size}
      x={position.x}
      y={position.y}
      style={{overflow: 'visible'}}
      xmlns="http://www.w3.org/2000/svg"
    >

      <defs>
        <filter id="c4blur" x="-50%" y="-50%" width='200%' height='200%'>
          <feGaussianBlur in="SourceGraphic" stdDeviation="4" />
        </filter>
      </defs>

      <g transform={rotation}>
        <g transform={translate}>
          <path
            d="M 45 0 A 45 45, 0, 1, 0, 90 45 L 90 0 Z"
            fill={colors[side]}
            strokeWidth={flashDuration * 12 / 4}
            strokeOpacity={flashDuration / 4}
            stroke='white'
          />

          { hasC4 && <circle r={20} fill={colors.tr} cx={-10} cy={100} filter="url(#c4blur)"/> }
          { isScoped && <line stroke={colors[side]} strokeWidth={8} x1={70} y1={20} x2={140} y2={-50} /> }
          { shotsFired && false &&
              <line
                stroke={`rgba(255, 255, 255, 0.5`}
                strokeWidth={6}
                strokeDasharray={25}
                strokeDashoffset={Math.random() * 25}
                strokeLinecap="round"
                x1={120} y1={-30} x2={430} y2={-340} />
          }

          { shotsFired &&
            <g>
              <line
                x1={100 + shotsDelta} x2={190 + shotsDelta}
                y1={-10 - shotsDelta} y2={-100 - shotsDelta}
                strokeWidth={5}
                stroke='#ccc'
              />
              <animateTransform
                attributeName="transform"
                attributeType="XML"
                type="translate"
                from="0 0"
                to="800 -800"
                dur="0.8s"
                begin="0s"
                repeatCount="indefinite"
              />
              <animate
                attributeType="CSS"
                attributeName="opacity"
                from="1"
                to="0"
                dur="0.8s"
                begin="0"
                repeatCount="indefinite" />
            </g>
          }
        </g>
      </g>

      <text {...textPlace} fontSize={80} fill='#666666'>{props.name}</text>
    </svg>
  )
}

PlayerPoint.propTypes = {
  /** Team side (ct or tr). */
  side: PropTypes.string,
}

export default PlayerPoint

