import React, { Component } from 'react'
import Progress from './EditableProgress'
import TimelineBar from './TimelineBar'
import { Button, Icon, Dropdown } from 'semantic-ui-react'

const moment = require('moment')

/**
 * PlaybackBar
 */
export default class PlaybackBar extends Component {
  static defaultProps = {
    speed: 1,
    paused: false,
    events: [],
    onResume: () => {},
    onPause: () => {}
  }

  onProgressChange = (percent, dragging) => {
    if (this.props.onSeek) {
      this.props.onSeek(this.props.max / 100 * percent, dragging)
    }
  }

  onToggle = _ => {
    this.props.paused
      ? this.props.onResume()
      : this.props.onPause()
  }

  render() {
    const speedOptions = [
      { key: 5, text: '0.5x', value: 0.5 },
      { key: 1, text: '1x', value: 1 },
      { key: 2, text: '2x', value: 2 },
      { key: 4, text: '4x', value: 4 },
    ]

    const currentTime = moment.duration(parseInt(this.props.currentTime, 10), 'seconds');
    const currentTimeString = moment.utc(currentTime.asMilliseconds()).format("mm:ss");
    const currentSpeed = speedOptions.find(s => s.value === this.props.speed).text
    const percent = (this.props.value / this.props.max) * 100

    const buttonStyle = {
      borderRadius: 0,
      margin: 0,
      color: 'rgba(255,255,255,0.7)',
      background: '#303030',
      width: 45,
      height: 40,
    }

    const timeRemainingStyle = {
      padding: '0 10px',
      background: '#303030',
      color: 'rgba(255,255,255,0.7)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      height: 40,
      width: 60,
    }

    const speedStyle = {
      background: '#303030',
      color: 'rgba(255,255,255,0.7)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      borderRadius: 0,
      width: 40,
      height: 40,
      margin: 0,
    }

    const progressContainerStyle = {
      position: 'relative',
      backgroundColor: '#212121',
      flexGrow: 1,
    }

    const progressStyle = {
      position: 'absolute',
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
    }

    return (
      <div style={{display: 'flex'}}>
        <Button
          style={buttonStyle}
          icon color='black'
          onClick={e =>this.onToggle()}>
            <Icon name={this.props.paused ? 'play' : 'pause'}/>
        </Button>

        <div style={progressContainerStyle}>
          <TimelineBar
            events={this.props.events}
            max={this.props.max}
          />

          <Progress
            style={progressStyle}
            value={this.props.value}
            total={this.props.max}
            onChange={this.onProgressChange}
          />
        </div>

        <div style={timeRemainingStyle}>{currentTimeString}</div>

        <Dropdown upward
          pointing='top left'
          icon={null}
          trigger={
            <Button
              style={speedStyle}
              content={currentSpeed}
            />
          }
          options={speedOptions}
          value={this.props.speed}
          onChange={(e, {value}) => this.props.onSpeedChange(value)}
        />
      </div>
    )
  }
}

