const fetch = require('node-fetch')
const debug = require('debug')('api')

const S3_HOST = 'https://gotactics.s3.amazonaws.com'
const CDN_HOST = 'https://d3raozhghzukbf.cloudfront.net'
const CURRENT_HOST = S3_HOST

const progress = (response, options = {}) => {
  if (!response.body.getReader) {
    return response
  }

  const reader = response.body.getReader();
  const contentSize = parseInt(response.headers.get('Content-Length'), 10);
  let downloadedLength = 0

  const stream = new ReadableStream({
    start(controller) {
      return pump();

      function pump() {
        return reader.read().then(({ done, value }) => {
          // When no more data needs to be consumed, close the stream
          if (done) {
            controller.close();
            return;
          }

          downloadedLength += value.length
          if (options.progress) {
            options.progress(downloadedLength, contentSize)
          }

          // Enqueue the next data chunk into our target stream
          controller.enqueue(value);
          return pump();
        });
      }
    }
  })

  return Promise.resolve(new Response(stream))
}

const operation = (name, fn) => (...args) => {
  debug('Running ' + name)
  return fn(...args)
}

const get = (url, options) => {
  return fetch(url)
    .then(res => res.ok ? res : Promise.reject({
      url,
      status: res.status,
      statusText: res.statusText
    }))
    .then(res => progress(res, options))
}

const getMatch = operation('getMatch', (id, options = {}) => (
  get(`${CURRENT_HOST}/matches/${id}.json`, options)
    .then(res => res.json())
))

const getDemoFile = operation('getDemoFile', (id, options = {}) => (
  get(`${CDN_HOST}/replays/${id}.replay`, options)
    .then(res => res.arrayBuffer())
    .then(buffer => Buffer.from(buffer))
))

const headDemoFile = operation('headDemoFile', id => (
  fetch({
    method: 'HEAD',
    url: `https://gotactics.s3.amazonaws.com/replays/${id}.replay`
  })
  .then(res => res.ok ? res : Promise.reject({
    status: res.status,
    statusText: res.statusText
  }))
))

module.exports = { getMatch, getDemoFile, headDemoFile }
