
import React from 'react';
import PlayerRow from './PlayerRow'
import colors from './colors'

/**
 * Team Panel
 * @example ./examples/TeamPanel.md
 */
const TeamPanel = ({players = [], name, side, score, style, logo}) => {
  const labels = {
    ct: 'Counter Terrorist',
    tr: 'Terrorist'
  }

  const headerStyle = {
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
    padding: 8,
    borderBottomWidth: 0,
    borderBottomColor: colors[side],
    borderBottomStyle: 'solid'
  }

  return (
    <div style={{...style}}>
      <div style={headerStyle}>
        {logo &&
          <div style={{padding: 2, marginRight: 10, borderRadius: '100%', background: 'white'}}>
            <img src={logo} className='ui avatar image' style={{margin: 0}}/>
          </div>
        }
        <div style={{flexGrow:1}}>
          <div style={{fontSize: 22, color: 'white' }}>{name || labels[side]}</div>
          { name && <div style={{fontSize: 12, color: colors[side]}}>{labels[side]}</div> }
        </div>
        <div style={{padding: 6, fontSize: 18, color: 'rgba(255, 255, 255, 0.7)'}}>{score}</div>
      </div>
      <div>
        {players.map((player,i) => {
          return (
            <div key={i} style={{paddingBottom: 1}}>
              <PlayerRow {...player} side={side} />
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default TeamPanel

