import React, { Component } from 'react'
import { Progress, Button, Label } from 'semantic-ui-react'
import Parser from 'gotactics-parser'

class FilePicker extends Component {
  onFileChange = ({currentTarget}) => {
    if (this.props.onChange && currentTarget.files.length > 0) {
      let files = []
      for (var i = 0; i < currentTarget.files.length; i++) {
        files.push(currentTarget.files.item(i))
      }
      this.props.onChange(files)
    }
    currentTarget.value = ''
  }

  onClick = () => {
    this.fileInput.click()
  }


  render() {
    const {children, ...props} = this.props
    return (
      <div>
        <div onClick={this.onClick}>{children}</div>
          <input
          {...props}
          type="file"
          style={{display: 'none'}}
          ref={(ref) => this.fileInput = ref}
          onChange={this.onFileChange}
        />
      </div>
    )
  }
}

const DemoPicker = ({onSelect, ...props}) => (
  <FilePicker
    onChange={files => onSelect(files.pop())}
    accept='.dem,.replay'
  >
    <Button {...props}/>
  </FilePicker>
)

class GamePicker extends Component {
  static defaultProps = {
    onComplete: () => {}
  }

  initialState = {
    progress: 0,
    game: undefined
  }

  constructor(props) {
    super(props)
    this.state = this.initialState
    this.parser = new Parser()
    this.parser.on('start', this.onStart)
    this.parser.on('progress', this.onProgress)
    this.parser.on('end', this.onEnd)
  }

  onDemoSelect = file => {
    this.setState({
      ...this.initialState,
      fileName: file.name,
    }, () => {
      this.readFile(file).then(buffer => this.parser.parse(buffer))
    })
  }

  readFile(file) {
    return new Promise((resolve, reject) => {
      var reader = new FileReader()
      reader.onload = e => resolve(e.target.result)
      reader.readAsArrayBuffer(file)
    })
  }

  onEnd = game => {
    this.setState({ progress: 100, game }, _ => {
      setTimeout(() => {
        this.props.onComplete(game)
      }, 1000)
    })
  }

  onStart = () => {
    this.setState({ progress: 0 })
  }

  onProgress = (progress) => {
    if (progress - this.state.progress > 1 || progress === 100) {
      this.setState({progress})
    }
  }

  render() {
    const { buttonProps } = this.props
    const { progress, fileName } = this.state
    return fileName
        ? (
          <Button as='div' labelPosition='right'>
            <Button {...buttonProps} loading/>
            <Label basic pointing='left'>
              <Progress
                size='tiny'
                percent={progress}
                style={{margin: 0, width: 80}}
                label='Loading Demo'
              />
            </Label>
          </Button>
        )
        : (
          <DemoPicker {...buttonProps} onSelect={this.onDemoSelect}/>
        )
  }
}

export default FilePicker
export {
  DemoPicker,
  GamePicker
}
