import React, { Component } from 'react'

import MiniMap from './MiniMap'
import PlaybackBar from './PlaybackBar'
import RoundPagination from './RoundPagination'
import DeathNotice from './DeathNotice'
import DemoDrawableBoard from './DemoDrawableBoard'
import TeamPanel from './TeamPanel'
import { Responsive, Item } from 'semantic-ui-react'
import Encoder from 'gotactics-parser/src/Encoder'

const TR_TEAM_NUMBER=2
const CT_TEAM_NUMBER=3

const TeamsSidePanel = ({style = {}, frame = {players: []}, round = {}, match, teamMap = {} }) => {
  const trs = frame.players.filter(p => p.teamNumber === 2)
  const cts = frame.players.filter(p => p.teamNumber === 3)

  const ctTeam = teamMap[cts[0].steam64Id]
  const trTeam = teamMap[trs[0].steam64Id]

  if (match && ctTeam && trTeam) {
    var ctLogo = (match.teams.find(t => t.name === ctTeam) || {}).logo
    var trLogo = (match.teams.find(t => t.name === trTeam) || {}).logo
  }

  const score = {
    tr: round.score ? round.score[2] : 0,
    ct: round.score ? round.score[3] : 0
  }

  const totalScore = score.tr + score.ct

  return (
    <div style={{display: 'flex', flexDirection: 'column', justifyContent: 'space-around', ...style}}>
      <TeamPanel side='tr' name={trTeam} logo={trLogo} score={score.tr} players={trs} />
      <TeamPanel side='ct' name={ctTeam} logo={ctLogo} score={score.ct} players={cts} />
    </div>
  )
}

const DeathNotices = ({deaths, style}) => {
  return (
    <div style={style}>
    { deaths.map((k,i) => (
      <div key={i} style={{margin: 5, maxWidth: 200}}>
        <DeathNotice {...k} />
      </div>
    ))}
    </div>
  )
}

export default class DemoPlayer extends Component {
  state = {
    game: undefined,
    tickNumbers: undefined,
    currentRound: undefined,
    currentTick: undefined,
    frame: undefined,
    playbackSpeed: 1,
    paused: true,
  }

  static defaultProps = {
    encodeType: 'avro'
  }

  componentDidMount() {
    this.encoder = Encoder(this.props.encodeType)
    if (this.props.game) {
      this.setupGame(this.props.game)
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.game !== nextProps.game) {
      if (nextProps.game) {
        this.setupGame(nextProps.game)
      } else {
        this.onPause()
        this.setState({
          game: undefined,
          tickNumbers: undefined,
          currentTick: undefined,
          frame: undefined
        })
      }
    }
  }

  setupGame = buffer => {
    const game = this.encoder.decodeGame(buffer)
    this.setState({
      game,
      tickNumbers: Object.keys(game.frames),
      currentTick: 0
    }, _ => {
      this.selectRound(1)
    })
  }

  selectRound = roundNumber => {
    const round = this.state.game.rounds.find(round => round.number === roundNumber)

    this.setState({
      currentRound: round
    }, _ => {
      this.selectTick(round ? round.startTick : 0)
    })
  }

  selectTick(tick) {
    let { teamMap } = this.state
    const { match } = this.props
    const { game, tickNumbers, currentRound } = this.state
    const { frames=[], rounds=[] } = game

    const nextTick = tickNumbers.find(n => parseInt(n, 10) >= parseInt(tick, 10))
    const nextRound = rounds.find(r => r.startTick <= nextTick && r.endTick > nextTick) || currentRound

    const frame = frames[nextTick]
      ? this.encoder.decodeFrame(frames[nextTick])
      : this.state.frame

    if (match && !teamMap) {
      const matchGame = match.games
        .find(g => game.mapName.includes(g.map.toLowerCase()))

      const teams = matchGame.score
        .filter(s => s.type == 't' || s.type == 'ct').slice(0, 2)
        .map(score => ({
          team: score.team,
          teamNumber: score.type === 't' ? TR_TEAM_NUMBER : CT_TEAM_NUMBER
        }))
        .reduce((acc, score) => ({
          ...acc,
          [score.teamNumber]: score.team
        }), {})

      teamMap = frame.players
        .reduce((map, player) => ({
          ...map,
          [player.steam64Id]: teams[player.teamNumber]
        }), {})
    }

    this.setState({
      frame,
      teamMap,
      currentTick: parseInt(nextTick, 10),
      currentRound: nextRound
    }, _ => {
      this.props.onNextTick({
        frame,
        tick: this.state.currentTick,
        round: this.state.currentRound
      })
    })
  }

  onResume = e => {
    const { game, playbackSpeed } = this.state

    if (this.state.playInterval) {
      clearInterval(this.state.playInterval)
    }

    this.setState({
      paused: false,
      playInterval: setInterval(_ => {
        this.selectTick(this.state.currentTick + 1)
      }, 1000 / game.tickRate / playbackSpeed)
    })
  }

  onPause = e => {
    if (this.state.playInterval) {
      clearInterval(this.state.playInterval)
    }
    this.setState({paused: true})
  }

  onSpeedChange = playbackSpeed => {
    this.setState({playbackSpeed}, _ => {
      if (!this.state.paused) {
        this.onResume()
      }
    })
  }

  renderPlayback() {
    const {
      currentTick, currentRound,
      frame, playbackSpeed, paused,
    } = this.state

    const tickInRound = currentTick - currentRound.startTick
    const roundTicks = currentRound.endTick - currentRound.startTick
    const events = currentRound.events
      .map(event => ({...event,
        time: event.tick - currentRound.startTick,
        body: JSON.parse(event.body)
      }))

    return  (
      <PlaybackBar
        value={tickInRound}
        max={roundTicks}
        currentTime={frame.time - currentRound.startTime}
        paused={paused}
        speed={playbackSpeed}
        onPause={this.onPause}
        onResume={this.onResume}
        onSeek={(tick, dragging) => {
          this.selectTick(currentRound.startTick + tick)
          if (dragging) {
            this.onPause()
          }
        }}
        onSpeedChange={this.onSpeedChange}
        events={events}
      />
    )
  }

  render() {
    const {
      game,
      currentRound,
      currentTick,
      frame,
      paused,
    } = this.state

    const deaths = (currentRound ? currentRound.events : [])
      .filter(e => e.type === 'death')
      .filter(e => e.tick < currentTick && e.tick - currentTick > -400)
      .map(e => JSON.parse(e.body))

    const playerStyle = {
      ...this.props.style,
      display: 'flex',
      flexDirection: 'column'
    }

    const containerStyle = {
      background: '#000000',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'strech',
      position: 'relative',
      flexGrow: 1
    }

    const mapSize = this.mapContainer
      ? this.mapContainer.getBoundingClientRect()
      : { width: 0, height: 0 }

    const mapSquareSize = {
      width: Math.min(mapSize.width, mapSize.height),
      height: Math.min(mapSize.width, mapSize.height)
    }

    const noticesStyle = {
      position: 'absolute',
      top: 0, right: 0,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'flex-end'
    }

    const match = this.props.match

    return (
      <div style={playerStyle}>
        <div ref={el => this.mapContainer = el} style={containerStyle}>

          { frame && game &&
            <div style={{display: 'flex', flexGrow: 1, justifyContent: 'center', alignItems: 'center', position: 'relative'}}>
              <MiniMap
                size={mapSquareSize}
                frame={frame}
                mapName={game.mapName}
                paused={this.state.paused}
              />
              <DeathNotices
                deaths={deaths}
                style={noticesStyle}
              />
            </div>
          }

          { frame &&
            <Responsive as={TeamsSidePanel}
              minWidth={mapSquareSize.width + 350}
              frame={frame}
              round={currentRound}
              style={{maxWidth: 350}}
              match={match}
              teamMap={this.state.teamMap}
            />
          }

          { frame && game &&
            <div style={{position: 'absolute', top: 0, left: 0, right: 0, bottom: 0}}>
              <DemoDrawableBoard
                style={{width: mapSize.width, height: mapSize.height}} />
            </div>
          }

          { match &&
            <div className="DemoPlayer--teams-badge">
              {
                match.teams.map(team =>
                  <div key={team.name} style={{color: 'white', margin: '10px'}}>
                    <img src={team.logo} className='ui avatar image'/>
                    <span>{team.name}</span>
                  </div>
                )
              }
            </div>
          }
        </div>

        { frame && this.renderPlayback() }
        { game &&
          <RoundPagination
            total={game.rounds.length}
            selected={currentRound ? currentRound.number : 0}
            onChange={this.selectRound}
          />
        }
      </div>
    )
  }
}
