import React, { Component } from 'react'

import DemoPlayer from './DemoPlayer'
import DemoPickerModal from './DemoPickerModal'
import { Responsive, Loader, Dimmer, Progress } from 'semantic-ui-react'

const Api = require('../lib/api')

/**
 * DemoViewer
 * @example ./examples/DemoViewer.md
 */
export default class DemoViewer extends Component {
  state = {
    frame: undefined,
    tick: undefined,
    game: undefined,
  }

  static defaultProps = {
    hltvId: undefined,
  }

  onComplete = game => {
    this.setState({game})
  }

  onScreenUpdate = () => {
    this.forceUpdate()
  }

  componentDidMount() {
    if (this.props.hltvId) {
      this.loadHltvMatch()
    }
  }

  componentWillReceiveProps({hltvId}) {
    if (hltvId !== this.props.hltvId && hltvId !== undefined) {
      this.setState({ game: undefined, error: undefined }, this.loadHltvMatch)
    }
  }

  loadHltvMatch() {
    if (this.props.hltvId !== undefined) {
      const matchId = this.props.hltvId.split('-')[0]

      Api
        .getDemoFile(this.props.hltvId, { progress: (value, total) => {
          this.setState({ hltvMatchProgress: value/total * 100 })
        }})
        .then(game => this.setState({game}))
        .catch(error => this.setState({error}))

      Api
        .getMatch(matchId)
        .then(match => this.setState({match}))
    }
  }

  render() {
    const { hltvId } = this.props
    const { game, match, error, hltvMatchProgress } = this.state

    const style = {
      height: '100vh',
      width: '100vw',
    }

    return (
      <Responsive
        as='div'
        onUpdate={this.onScreenUpdate}
        style={style}
        fireOnMount
      >

        <div style={{display: 'flex', height: '100%', width: '100%', flexDirection: 'row', backgroundColor: '#000000', padding: 5}}>

          <DemoPlayer
            game={game}
            match={match}
            style={{flexGrow: 1, minHeight: 300, minWidth: 300}}
            onNextTick={props => this.setState(props)}/>

          { error &&
            <Dimmer active>
              <pre>{JSON.stringify(error, null, 4)}</pre>
            </Dimmer>
          }
          { (!game && !error && hltvMatchProgress > 0) &&
            <Dimmer active>
              <Loader>{hltvId}</Loader>
              <Progress
                style={{width: 200, margin: '0 auto', position: 'relative', top: 50}}
                size='small'
                percent={hltvMatchProgress}
                progress
                indicating
                precision={0}
              />
            </Dimmer>
          }

        </div>

        { !game && !hltvId &&
          <DemoPickerModal
            defaultOpen={true}
            onComplete={this.onComplete}
            closeOnDimmerClick={false}
            closeOnEscape={false}
            closeOnRootNodeClick={false}
          />
        }
      </Responsive>
    )
  }
}
