import React from 'react'
import PropTypes from 'prop-types'

const weapons = [
  { regex: /^1$|deagle/i, component: require('./weapons/Deagle').default, order: 2 },
  { regex: /^2$|dual/i, component: require('./weapons/Dual').default, order: 2 },
  { regex: /^3$|fiveseven/i, component: require('./weapons/Fiveseven').default, order: 2 },
  { regex: /^4$|glock/i, component: require('./weapons/Glock').default, order: 2 },
  { regex: /^7$|ak/i, component: require('./weapons/Ak47').default, order: 1 },
  { regex: /^8$|aug/i, component: require('./weapons/Aug').default, order: 1 },
  { regex: /^9$|awp/i, component: require('./weapons/Awp').default, order: 1 },
  { regex: /^10$|famas/i, component: require('./weapons/Famas').default, order: 1 },
  { regex: /^11$|g3sg1/i, component: require('./weapons/G3Sg1').default, order: 1 },
  { regex: /^26$|bizon/i, component: require('./weapons/Bizon').default, order: 1 },
  { regex: /^13$|galil/i, component: require('./weapons/Galilar').default, order: 1 },
  { regex: /^14$|m249/i, component: require('./weapons/M249').default, order: 1 },
  { regex: /^16$|m4a4/i, component: require('./weapons/M4A1').default, order: 1 },
  { regex: /^17$|mac10/i, component: require('./weapons/Mac10').default, order: 1 },
  { regex: /^19$|p90/i, component: require('./weapons/P90').default, order: 1 },
  { regex: /^24$|ump.*/i, component: require('./weapons/Ump45').default, order: 1 },
  { regex: /^25$|xm.*/i, component: require('./weapons/Xm1014').default, order: 1 },
  { regex: /^27$|mag.*/i, component: require('./weapons/Mag7').default, order: 1 },
  { regex: /^28$|negev.*/i, component: require('./weapons/Negev').default, order: 1 },
  { regex: /^29$|sawed.*/i, component: require('./weapons/Sawedoff').default, order: 1 },
  { regex: /^30$|tec9/i, component: require('./weapons/Tec9').default, order: 2 },
  { regex: /^31$|zeus/i, component: require('./weapons/Zeus').default, order: 3 },
  { regex: /^32$|p2000/i, component: require('./weapons/P2000').default, order: 2 },
  { regex: /^33$|mp7/i, component: require('./weapons/Mp7').default, order: 1 },
  { regex: /^34$|mp9/i, component: require('./weapons/Mp9').default, order: 1 },
  { regex: /^35$|nova/i, component: require('./weapons/Nova').default, order: 1 },
  { regex: /^36$|p250/i, component: require('./weapons/P250').default, order: 2 },
  { regex: /^38$|scar/i, component: require('./weapons/Scar20').default, order: 2 },
  { regex: /^39$|sg5.*/i, component: require('./weapons/Sg556').default, order: 2 },
  { regex: /^40$|ssg.*|scout/i, component: require('./weapons/Ssg08').default, order: 1 },
  { regex: /^42$|59|knife/i, component: require('./weapons/Knife').default, order: 8 },
  { regex: /^43$|flash|bang/i, component: require('./weapons/Flashbang').default, order: 4 },
  { regex: /^44$|^he$|hegrenade/i, component: require('./weapons/Hegrenade').default, order: 3 },
  { regex: /^45$|smoke.*/i, component: require('./weapons/Smokegrenade').default, order: 5 },
  { regex: /^46$|molotov/i, component: require('./weapons/Molotov').default, order: 6 },
  { regex: /^47$|decoy/i, component: require('./weapons/Decoy').default, order: 7 },
  { regex: /^48$|incendiary/i, component: require('./weapons/Incendiary').default, order: 6 },
  { regex: /^49$|c4$/i, component: require('./weapons/C4').default, order: 9 },
  { regex: /c4-ticking/i, component: require('./weapons/C4Ticking').default, order: 9 },
  { regex: /^50$|kevlar|armor/i, component: require('./weapons/Armor').default, order: 10 },
  { regex: /^51$|helmet/i, component: require('./weapons/Helmet').default, order: 10 },
  { regex: /^55$|defuser/i, component: require('./weapons/Defuser').default, order: 10 },
  { regex: /^60$|m4a1/i, component: require('./weapons/M4A1Silencer').default, order: 1 },
  { regex: /^61$|usp/i, component: require('./weapons/UspSilencer').default, order: 2 },
  { regex: /^63$|cz/i, component: require('./weapons/Cz75A').default, order: 2 },
  { regex: /^64$|r8|revolver/i, component: require('./weapons/Revolver').default, order: 2 },
  { regex: /^500|505|506|507|508|509|512|514|515|516$/i, component: require('./weapons/Knife').default, order: 8 },
  { regex: /hs/i, component: require('./weapons/Headshot').default },
  { regex: /.*/i, component: () => null }
]

/**
 * Weapon
 */
const Weapon = props => {
  const Component = weapons
    .find(w => w.regex.test(props.id))
    .component

  return (<Component {...props} />)
}

Weapon.propTypes = {
  /** Weapon identification. */
  id: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ])
}

const getOrder = id => weapons
  .find(w => w.regex.test(id))
  .order

export default Weapon
export {
  getOrder
}


