import React from 'react';
import PlayerPoint from './PlayerPoint'
import GrenadePoint from './GrenadePoint'
import C4Point from './C4Point'


const defaultPlaceFunction = (coords, xAdjuster=1, yAdjuster=1) => size => {
  const scaleX = scale({
    domain: coords.x,
    range: [(size.width * xAdjuster) - size.width, size.width * xAdjuster]
  })

  const scaleY = scale({
    domain: coords.y,
    range: [size.height * yAdjuster, (size.height * yAdjuster) - size.height]
  })

  return position => ({
    x: scaleX(position.x),
    y: scaleY(position.y)
  })
}

const scale = ({domain, range}) => {
  const [istart, istop] = domain
  const [ostart, ostop] = range
  return value => ostart + (ostop - ostart) * ((value - istart) / (istop - istart));
}

const maps = {
  'de_cache': {
    href: require('../resources/radars/panorama/de_cache_radar.png'),
    place: defaultPlaceFunction({
      x: [-2100, 3800],
      y: [-2380, 3300]
    })
  },
  'de_cbble': {
    href: require('../resources/radars/panorama/de_cbble_radar.png'),
    place: defaultPlaceFunction({
      x: [-3840, 2300],
      y: [-3072, 3072]
    })
  },
  'de_inferno': {
    href: require('../resources/radars/panorama/de_inferno_radar.png'),
    place: defaultPlaceFunction({
      x: [-2140, 2960],
      y: [-1150, 3900]
    })
  },
  'de_mirage': {
    href: require('../resources/radars/panorama/de_mirage_radar.png'),
    place: defaultPlaceFunction({
      x: [-3140, 1900],
      y: [-3370, 1800]
    })
  },
  'de_overpass': {
    href: require('../resources/radars/panorama/de_overpass_radar.png'),
    place: defaultPlaceFunction({
      x: [-4880, 520],
      y: [-3560, 1800]
    })
  },
  'de_train': {
    href: require('../resources/radars/panorama/de_train_radar.png'),
    place: defaultPlaceFunction({
      x: [-2500, 2350],
      y: [-2470, 2450]
    })
  },
  'de_dust2': {
    href: require('../resources/radars/panorama/de_dust2_radar.png'),
    place: defaultPlaceFunction({
      x: [-2450, 2050],
      y: [-1300, 3230]
    })
  },
  'de_nuke': {
    href: require('../resources/radars/panorama/de_nuke_both.png'),
    place: size => {
      const upperPlace = defaultPlaceFunction({
        x: [-3500, 3800],
        y: [-4300, 2900],
      }, 1, 0.8583)(size)

      const lowerPlace = defaultPlaceFunction({
        x: [-3500, 3800],
        y: [-4300, 2900],
      }, 0.75, 1.21)(size)


      return position => {
        if (position.z > -430) {
          return upperPlace(position)
        } else {
          return lowerPlace(position)
        }
      }
    }
  }
}

class MiniMap extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  componentDidMount() {
    if (this.props.mapName && this.props.size) {
      this.updateMap(this.props.mapName, this.props.size)
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.mapName !== this.props.mapName || prevProps.size !== this.props.size) {
      this.updateMap(this.props.mapName, this.props.size)
    }
  }

  updateMap(mapName, size) {
    const map = maps[mapName]
    this.setState({placeFunction: map.place(size)})
  }

  render() {
    const sides = ['spec', 'spec', 'tr', 'ct']
    const { mapName, frame = {}, size, paused } = this.props
    const mapImage = maps[mapName].href
    const place = this.state.placeFunction || function() {}

    const {
      players = [], grenades = [],
      c4status = 'pickup',
      c4position,
    } = frame

    return (
      <svg {...size}>
        <image href={mapImage} {...size} />

        { players.filter(p => p.health > 0).map((player, i) =>
          <PlayerPoint
            key={`player-${i}`}
            size={12}
            side={sides[player.teamNumber]}
            {...player}
            position={place(player.position || {})}
            shotsFired={paused ? 0 : player.shotsFired}
          />
        ) }

        { grenades.map((g,i) => (
            <GrenadePoint key={`grenade-${i}`} type={g.type} detonating={g.isDetonated} {...place(g.position)}/>
        )) }

        { (c4status !== 'pickup')  &&
          <C4Point {...place(c4position)} status={c4status}/>
        }

      </svg>
    )
  }
}

export default MiniMap
