import React from 'react'
import PropTypes from 'prop-types'
import CircularProgress from './CircularProgress'
import Weapon from './Weapon'
import colors from './colors'

const PlayerCircularInfo = props => {
  const {
    health = 100,
    hasArmor = false,
    hasHelmet = false,
    hasDefuser = false,
    side = 'spec',
    textColor,
    size = 60,
    style,
  } = props

  return (
    <svg width={size} height={size} overflow='visible' viewBox='0 0 45 45' style={style}>

      <g transform="translate(7,7)">
        <CircularProgress
          fontSize={30}
          percentage={health}
          text={health.toString()}
          textColor={textColor}
          size={35}
          progressColor={colors[side]}
          backgroundColor='rgba(0,0,0,0.4)'
          strokeWidth={6}/>
      </g>

      { !hasHelmet && hasArmor &&
        <Weapon y={30} x={30} id='armor' width={12} height={12} fill='#e4ebf5'/>
      }
      { hasHelmet &&
        <Weapon y={30} x={30} id='helmet' width={12} height={12} fill='#e4ebf5'/>
      }
      { hasDefuser &&
        <g>
          <circle r={6} fill='#e4ebf5' cx={9} cy={9}/>
          <Weapon y={5} x={5} id='defuser' width={8} height={8} fill='black' fillOpacity={0.7}/>
        </g>
      }

    </svg>
  )
}

PlayerCircularInfo.propTypes = {
  /** Player's Health. */
  health: PropTypes.number,

  /** Has armor or not. */
  // hasArmor: PropTypes.boolean,

  /** Has helmet or not. */
  // hasHelmet: PropTypes.boolean,

  /** Has kit or not. */
  // hasDefuser: PropTypes.boolean,

  /** Team side (ct or tr). */
  side: PropTypes.string,
}

export default PlayerCircularInfo

